import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import {
  getCartTotal,
  removeItem,
  decreaseItemQuantity,
  increaseItemQuantity,
} from "../features/cartSlice";

const CartPage = () => {
  const { cart, totalQuantity, totalPrice } = useSelector(
    (state) => state.allCart
  );
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getCartTotal());
  }, [cart, dispatch]);

  const handleRemoveItem = (id) => {
    dispatch(removeItem(id));
  };

  const handleDecreaseItemQuantity = (id) => {
    dispatch(decreaseItemQuantity(id));
  };

  const handleIncreaseItemQuantity = (id) => {
    dispatch(increaseItemQuantity(id));
  };

  const renderCartItems = () => {
    return cart.map((data) => (
      <div className="row" key={data.id}>
        <div className="col-lg-3 col-md-12 mb-4 mb-lg-0">
          <div
            className="bg-image hover-overlay hover-zoom ripple rounded"
            data-mdb-ripple-color="light"
          >
            <img
              src={data.img}
              className="w-100"
              alt={data.title}
            />
          </div>
        </div>

        <div className="col-lg-5 col-md-6 mb-4 mb-lg-0">
          <p>
            <strong>{data.title}</strong>
          </p>

          <button
            type="button"
            className="butt btn-sm me-1 mb-2"
            data-mdb-toggle="tooltip"
            title="Remove item"
            onClick={() => handleRemoveItem(data.id)}
          >
            <i className="fas fa-trash"></i>
          </button><br/>
          <p>#{data.price}</p>
        </div>

        <div className="col-lg-4 col-md-6 mb-4 mb-lg-0">
          <div className="d-flex mb-4" style={{ maxWidth: "300px" }}>
            <button
              className="butt px-3 me-2"
              onClick={() => handleDecreaseItemQuantity(data.id)}
            >
              <i className="fas fa-minus"></i>
            </button>

            <div className="form-outline">
              <label className="form-label" htmlFor="form1">
                {data.quantity}
              </label>
            </div>

            <button
              className="butt px-3 ms-2"
              onClick={() => handleIncreaseItemQuantity(data.id)}
            >
              <i className="fas fa-plus"></i>
            </button>
          </div>

          <p className="text-start text-md-center">
            <strong>#{data.quantity * data.price}</strong>
          </p>
        </div>
        <hr className="my-4" />
      </div>
    ));
  };

  return (
    <div>
      <section className="h-100 gradient-custom">
        <div className="container py-5">
          <div className="row d-flex justify-content-center my-4">
            <div className="col-md-8">
              <div className="card mb-4">
                <div className="card-header py-3">
                  <h5 className="mb-0">Your Order - {cart.length} items</h5>
                </div>
                <div className="card-body">{renderCartItems()}</div>
              </div>
            </div>          

            <div >
              <div className="card mb-4">
                <div className="card-header py-3">
                  <h5 className="mb-0">Your Bill</h5>
                </div>
                <div className="card-body">
                  <ul className="list-group list-group-flush">
                    <li className="list-group-item d-flex justify-content-between align-items-center border-0 px-0 pb-0">
                      Total Quantity
                      <span>{totalQuantity}</span>
                    </li>

                    <li className="list-group-item d-flex justify-content-between align-items-center border-0 px-0 mb-3">
                      <div>
                        <strong>Total amount</strong>
                      </div>
                      <span>
                        <strong>#{totalPrice}</strong>
                      </span>
                    </li>
                  </ul>

                  <button
                    type="button"
                    className=" btn-lg btn-block checkout"
                    
                  ><a href="/home" style={{color: 'gold'}}>
                    Go to checkout
                    </a>  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default CartPage;
